body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F6F6F6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.outer{

  padding-top: 3%;
}

.cardclass {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 5%;
}
.card {
  margin-bottom: 5%;
}

.nodata {
  width: 100%;
  display: flex;
  justify-content: center;
  color: #9b9b9b;
  font-size: 20px;
  margin-top: 10%;
  font-weight: bold;
}

.card-text {
  overflow: hidden;
  position: relative;
  line-height: 1.2em;
  max-height: 4.8em;
  text-align: justify;
  margin-right: -1em;
  padding-right: 1em;
}

.card-text:before {
  content: "...";
  position: absolute;
  right: 0;
  bottom: 0;
}

.card-text:after {
  content: "";
  position: absolute;
  right: 0;
  width: 1em;
  height: 1em;
  margin-top: 0.2em;
  background: white;
}

.container {
  background: #ffffff;
  box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.08);
  border-radius: 8px;
  padding:1%;
  margin:auto
}
.nodata{
  width: 100%;
  display: flex;
  justify-content: center;
  color: #9b9b9b;
  font-size: 20px;
  margin-top: 10%;
  font-weight: bold;
}
